import axios from "axios";

const url = "http://localhost:8080/api/";

export default {
  login(credentials) {
    return axios
      .post(url + "login/", credentials)
      .then((response) => response.data);
  },
  signUp(credentials) {
    return axios
      .post(url + "sign-up/", credentials)
      .then((response) => response.data);
  },
  getTestAdminRoute() {
    return axios
      .get(url + "test-admin-route/")
      .then((response) => response.data);
  },
};
